.portfolio {
  padding: 120px 20px;
  background: linear-gradient(135deg, #f5f5f5, #ffffff);
  min-height: 100vh;

  h2 {
    text-align: center;
    color: #333;
    font-size: 3rem;
    margin-bottom: 5rem;
    position: relative;
    
    &::after {
      content: '';
      position: absolute;
      bottom: -10px;
      left: 50%;
      transform: translateX(-50%);
      width: 60px;
      height: 3px;
      background: #333;
    }
  }

  .portfolio-container {
    max-width: 1400px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 8rem;
    padding: 0 20px;
  }

  .portfolio-card {
    display: flex;
    background: #ffffff;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    height: 600px;
    
    &:hover {
      transform: translateY(-10px);
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
      
      .card-image img {
        transform: scale(1.05);
      }
    }

    &.design {
      background: #fafafa;
      border-color: rgba(0, 0, 0, 0.08);
      
      &[data-horizontal="true"] {
        display: flex;
        flex-direction: column;
        padding: 2rem;
        height: auto;
        position: relative;

        .card-image {
          display: flex;
          justify-content: center;
          gap: 2rem;
          height: auto;
          margin-bottom: 1.5rem;

          img {
            width: calc(50% - 1rem);
            height: 600px;
            object-fit: contain;
            background: #fff;
            border-radius: 12px;
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
            cursor: pointer;
            transition: all 0.3s ease;
            padding: 0.5rem;

            &:hover {
              transform: translateY(-5px);
              box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
            }

            &.expanded {
              position: fixed;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              width: auto;
              height: 90vh;
              max-width: 90vw;
              object-fit: contain;
              background: #fff;
              padding: 1rem;
              z-index: 1000;
              cursor: zoom-out;
            }
          }

          .image-indicators {
            display: none;
          }
        }

        .card-content {
          text-align: center;
          padding: 2rem;
          background: transparent;
          position: relative;
          z-index: 1;

          .project-type {
            margin: 0 auto 1.5rem;
            font-size: 1.5rem;
            color: #333;
          }

          .title-container {
            display: flex;
            justify-content: center;
            gap: 2rem;
            margin-bottom: 1rem;

            h3 {
              flex: 1;
              max-width: calc(50% - 1.5rem);
              font-size: 2rem;
              margin: 0;
              text-align: center;
              color: #2c3e50;
            }
          }

          .description-container {
            display: flex;
            justify-content: center;
            gap: 2rem;
            margin-bottom: 1.5rem;

            p {
              flex: 1;
              max-width: calc(50% - 1.5rem);
              margin: 0;
              text-align: center;
              font-size: 1.2rem;
              line-height: 1.5;
              color: #555;
            }
          }

          .skills-used {
            justify-content: center;
            margin-top: 0.5rem;

            .skill-tag {
              background: rgba(0, 0, 0, 0.05);
              padding: 8px 16px;
              border-radius: 20px;
              font-size: 1rem;
              color: #444;
            }
          }
        }

        .overlay {
          position: fixed;
          top: 0;
          left: 0;
          width: 100vw;
          height: 100vh;
          background: rgba(0, 0, 0, 0.8);
          z-index: 999;
          opacity: 0;
          visibility: hidden;
          transition: all 0.3s ease;

          &.active {
            opacity: 1;
            visibility: visible;
          }
        }
      }
    }

    .card-image {
      flex: 1.2;
      position: relative;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: opacity 0.5s ease, transform 0.5s ease;
        
        &.image-transition {
          position: absolute;
          top: 0;
          left: 0;
          opacity: 1;
          
          &.fade {
            opacity: 0.9;
          }
        }
      }

      .image-indicators {
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        gap: 10px;
        z-index: 2;

        span {
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: rgba(255, 255, 255, 0.5);
          transition: all 0.3s ease;

          &.active {
            background: #fff;
            transform: scale(1.2);
          }
        }
      }

      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: opacity 0.3s ease;

        a {
          color: #fff;
          text-decoration: none;
          padding: 15px 30px;
          border: 2px solid #fff;
          border-radius: 30px;
          font-weight: 500;
          font-size: 1.1rem;
          transition: all 0.3s ease;

          &:hover {
            background: #fff;
            color: #333;
          }
        }
      }
    }

    .card-content {
      flex: 1;
      padding: 4rem;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      overflow-y: auto;
      

      .project-type {
        font-size: 1rem;
        color: #666;
        margin-bottom: 1.5rem;
        display: inline-block;
        padding: 6px 16px;
        background: rgba(0, 0, 0, 0.05);
        border-radius: 20px;
        align-self: flex-start;
      }

      h3 {
        font-size: 2.5rem;
        margin-bottom: 1.5rem;
        color: #222;
        line-height: 1.3;
      }

      .project-duration {
        margin-bottom: 1.5rem;
        font-size: 1.1rem;
        color: #555;

        .duration-label {
          font-weight: 500;
          margin-right: 0.5rem;
        }

        .duration-value {
          color: #666;
        }
      }

      p {
        margin-bottom: 3rem;
        color: #666;
        line-height: 1.8;
        font-size: 1.2rem;
      }

      .skills-used {
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
        margin-top: auto;

        .skill-tag {
          background: rgba(0, 0, 0, 0.05);
          padding: 8px 16px;
          border-radius: 20px;
          font-size: 1rem;
          color: #444;
        }
      }
    }

    .project-link {
      margin-top: 4rem;
      text-align: center;

      a {
        font-size: 1.2rem;
        color: #2980b9;
        text-decoration: none;
        transition: color 0.3s ease;

        &:hover {
          color: #1abc9c;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .portfolio {
    .portfolio-card {
      height: 500px;
    }
  }
}

@media (max-width: 968px) {
  .portfolio {
    .portfolio-card {
      flex-direction: column;
      height: auto;

      .card-image {
        height: 400px;
      }

      .card-content {
        padding: 2.5rem;
        max-height: 500px;
        overflow-y: auto;
      }

      &.design[data-horizontal="true"] {
        flex-direction: column;
        height: auto;

        .card-image {
          flex-direction: column;
          height: auto;

          img {
            width: 100%;
            height: 600px;
            object-fit: contain;
          }
        }

        .card-content {
          position: relative;
          background: #fafafa;
          padding: 2rem;

          .title-container,
          .description-container {
            flex-direction: column;
            gap: 1rem;

            h3, p {
              width: 100%;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .portfolio {
    padding: 80px 10px;

    h2 {
      font-size: 2.5rem;
      margin-bottom: 3rem;
    }

    .portfolio-container {
      gap: 4rem;
      padding: 0 15px;
    }

    .portfolio-card {
      .card-image {
        height: 300px;
      }
    }
  }
}
