.intro {
  width: 100%;
  height: 200vh;
  background: linear-gradient(135deg, #4B0082, #000080);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  overflow: hidden;
  padding-top: 30vh;
}

.starBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.star {
  position: absolute;
  fill: url(#starGradient);
  opacity: 0.6;
  will-change: transform;
  
  &:nth-child(3n) {
    animation: floatAnimation1 30s infinite linear;
  }
  &:nth-child(3n+1) {
    animation: floatAnimation2 35s infinite linear;
  }
  &:nth-child(3n+2) {
    animation: floatAnimation3 40s infinite linear;
  }
}

@keyframes floatAnimation1 {
  0% { transform: translateY(0); }
  50% { transform: translateY(-80px) translateX(40px); }
  100% { transform: translateY(0); }
}

@keyframes floatAnimation2 {
  0% { transform: translateY(-20px); }
  50% { transform: translateY(80px) translateX(-40px); }
  100% { transform: translateY(-20px); }
}

@keyframes floatAnimation3 {
  0% { transform: translateY(20px); }
  50% { transform: translateY(-60px) translateX(60px); }
  100% { transform: translateY(20px); }
}

.container {
  text-align: center;
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
}

.title {
  font-family: 'Montserrat', sans-serif;
  color: #FFF;
  position: relative;
  padding-top: 100px;
  
  .kyuText {
    position: absolute;
    color: #DFFF1A;
    font-size: 2rem;
    opacity: 1;
    visibility: visible;
    left: 420px;
    top: 80px;
  }

  span:last-child {
    font-size: 7rem;
    font-weight: 700;
    letter-spacing: 0.7rem;
    display: flex;
    gap: 0.2rem;
    justify-content: center;
  }

  .portfolioLetter {
    display: inline-block;
    opacity: 0;
    font-size: inherit;
    font-weight: inherit;
    color: #FFF;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    
    &:hover {
      color: var(--space-yellow);
      transform: translateY(-2px);
      transition: all 0.3s ease;
    }
  }
}

.hello {
  position: absolute;
  font-size: 2.5rem;
  color: #DFFF1A;
  font-family: 'Bangers', cursive;
  right: 520px;
  top: 231px;
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 100;
  transition: transform 0.8s ease-in-out;

  .loadingContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }

  .loadingBar {
    width: 200px;
    height: 2px;
    background: rgba(255, 255, 255, 0.2);
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 0;
      background: #FFF;
      animation: loadingProgress 2s ease-in-out;
    }
  }

  .loadingTextWrapper {
    perspective: 1000px;
  }

  .loadingText {
    display: flex;
    gap: 0.5rem;
    color: #FFF;
    font-family: 'Montserrat', sans-serif;
  }

  .loadingLetter {
    display: inline-block;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 0.1em;
    color: #FFF;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    transform-origin: bottom center;
    will-change: transform;
  }
}

@keyframes loadingProgress {
  0% { width: 0; }
  100% { width: 100%; }
}

@media (max-width: 768px) {
  .title {
    .kyuText {
      font-size: 1.8rem;
      top: -40px;
    }

    span:last-child {
      font-size: 4rem;
      letter-spacing: 0.5rem;
      gap: 0.1rem;
    }
  }

  .hello {
    font-size: 1.8rem;
    right: 10px;
    top: 10px;
    transform: rotate(-10deg);
  }
}
