.header {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  width: 60%;
  max-width: 600px;
}

.nav {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 30px;
  padding: 15px 25px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navList {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  margin: 0;
  padding: 0;

  li {
    margin: 0 10px;

    a {
      text-decoration: none;
      color: #333;
      font-weight: 700;
      font-size: 16px;
      transition: all 0.3s ease;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;

      .icon {
        font-size: 20px;
        margin-bottom: 2px;
        color: var(--space-blue);
        transition: all 0.3s ease;
      }

      span {
        font-size: 16px;
        transition: all 0.3s ease;
      }

      &:hover {
        color: var(--space-blue);
        transform: translateY(-3px);

        .icon {
          color: var(--space-purple);
          transform: scale(1.1);
        }

        span {
          color: var(--space-purple);
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .header {
    width: 90%;
    bottom: 15px;
  }

  .nav {
    padding: 10px 20px;
  }

  .navList {
    li {
      margin: 0 5px;
      
      a {
        .icon {
          font-size: 18px;
        }

        span {
          font-size: 14px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .navList {
    li {
      a {
        .icon {
          font-size: 16px;
        }

        span {
          font-size: 12px;
        }
      }
    }
  }
}
