.skills {
  padding: 8rem 2rem;
  background: linear-gradient(135deg, var(--space-blue), var(--space-purple));
  color: white;

  h2 {
    font-size: 4rem;
    text-align: center;
    margin-bottom: 3rem;
  }

  .skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }

  .skill-item {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 1.5rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 200px;
    position: relative;

    .skill-icon-wrapper {
      width: 60px;
      height: 60px;
      margin: 0 auto 1rem;
      background-color: white;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
    }

    .skill-icon {
      width: 40px;
      height: 40px;
      object-fit: contain;
    }

    h3 {
      font-size: 1rem;
      color: var(--space-yellow);
      margin-bottom: 1rem;
    }

    .skill-info {
      margin-top: 1rem;
    }

    .skill-bar {
      background-color: rgba(255, 255, 255, 0.1);
      height: 6px;
      border-radius: 3px;
      overflow: hidden;
      margin-bottom: 0.5rem;
    }

    .skill-level {
      height: 100%;
      background: linear-gradient(90deg, var(--space-yellow), var(--space-pink));
      position: relative;
      width: 0%;
      transition: width 1s ease-out;
      
      .skill-percentage {
        position: absolute;
        right: 0;
        top: -20px;
        font-size: 0.75rem;
        color: var(--space-yellow);
        white-space: nowrap;
        transform: translateX(50%);
      }

      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 4px;
        background-color: var(--space-yellow);
        opacity: 0.5;
      }
    }

    .skill-description {
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.8);
      margin-top: 0.5rem;
      line-height: 1.4;
    }
  }

  @media (max-width: 768px) {
    padding: 6rem 1rem;

    h2 {
      font-size: 3rem;
    }

    .skill-item {
      width: calc(50% - 1rem);
    }
  }

  @media (max-width: 480px) {
    .skill-item {
      width: 100%;
    }
  }
}
